import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import xueshengList from '@/views/pages/xuesheng/list'
import xueshengDetail from '@/views/pages/xuesheng/formModel'
import xueshengAdd from '@/views/pages/xuesheng/formAdd'
import xueshengRegister from '@/views/pages/xuesheng/register'
import xueshengCenter from '@/views/pages/xuesheng/center'
import jiaoshiList from '@/views/pages/jiaoshi/list'
import jiaoshiDetail from '@/views/pages/jiaoshi/formModel'
import jiaoshiAdd from '@/views/pages/jiaoshi/formAdd'
import banjixinxiList from '@/views/pages/banjixinxi/list'
import banjixinxiDetail from '@/views/pages/banjixinxi/formModel'
import banjixinxiAdd from '@/views/pages/banjixinxi/formAdd'
import kechengxinxiList from '@/views/pages/kechengxinxi/list'
import kechengxinxiDetail from '@/views/pages/kechengxinxi/formModel'
import kechengxinxiAdd from '@/views/pages/kechengxinxi/formAdd'
import kechengbaomingList from '@/views/pages/kechengbaoming/list'
import kechengbaomingDetail from '@/views/pages/kechengbaoming/formModel'
import kechengbaomingAdd from '@/views/pages/kechengbaoming/formAdd'
import kechengfenleiList from '@/views/pages/kechengfenlei/list'
import kechengfenleiDetail from '@/views/pages/kechengfenlei/formModel'
import kechengfenleiAdd from '@/views/pages/kechengfenlei/formAdd'
import banjimingchengList from '@/views/pages/banjimingcheng/list'
import banjimingchengDetail from '@/views/pages/banjimingcheng/formModel'
import banjimingchengAdd from '@/views/pages/banjimingcheng/formAdd'
import exampaperList from '@/views/pages/exam/exampaperList'
import examination from '@/views/pages/exam/examination'
import examrecordList from '@/views/pages/exam/examrecordList'
import examfailrecord from '@/views/pages/exam/examfailrecord'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'xueshengList',
			component: xueshengList
		}, {
			path: 'xueshengDetail',
			component: xueshengDetail
		}, {
			path: 'xueshengAdd',
			component: xueshengAdd
		}
		, {
			path: 'xueshengCenter',
			component: xueshengCenter
		}
		, {
			path: 'jiaoshiList',
			component: jiaoshiList
		}, {
			path: 'jiaoshiDetail',
			component: jiaoshiDetail
		}, {
			path: 'jiaoshiAdd',
			component: jiaoshiAdd
		}
		, {
			path: 'banjixinxiList',
			component: banjixinxiList
		}, {
			path: 'banjixinxiDetail',
			component: banjixinxiDetail
		}, {
			path: 'banjixinxiAdd',
			component: banjixinxiAdd
		}
		, {
			path: 'kechengxinxiList',
			component: kechengxinxiList
		}, {
			path: 'kechengxinxiDetail',
			component: kechengxinxiDetail
		}, {
			path: 'kechengxinxiAdd',
			component: kechengxinxiAdd
		}
		, {
			path: 'kechengbaomingList',
			component: kechengbaomingList
		}, {
			path: 'kechengbaomingDetail',
			component: kechengbaomingDetail
		}, {
			path: 'kechengbaomingAdd',
			component: kechengbaomingAdd
		}
		, {
			path: 'kechengfenleiList',
			component: kechengfenleiList
		}, {
			path: 'kechengfenleiDetail',
			component: kechengfenleiDetail
		}, {
			path: 'kechengfenleiAdd',
			component: kechengfenleiAdd
		}
		, {
			path: 'banjimingchengList',
			component: banjimingchengList
		}, {
			path: 'banjimingchengDetail',
			component: banjimingchengDetail
		}, {
			path: 'banjimingchengAdd',
			component: banjimingchengAdd
		}
		, {
			path: 'exampaperList',
			component: exampaperList
		}, {
			path: 'examrecordList',
			component: examrecordList
		}, {
			path: 'examfailrecord',
			component: examfailrecord
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/xueshengRegister',
		component: xueshengRegister
	}
	, {
		path: '/examination',
		component: examination
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
